/**
 * TravelportHotel model events
 */

'use strict';

import {EventEmitter} from 'events';
import TravelportHotel from './travelportHotel.model';
var TravelportHotelEvents = new EventEmitter();

// Set max event listeners (0 == unlimited)
TravelportHotelEvents.setMaxListeners(0);

// Model events
var events = {
  save: 'save',
  remove: 'remove'
};

// Register the event emitter to the model events
for(var e in events) {
  let event = events[e];
  TravelportHotel.schema.post(e, emitEvent(event));
}

function emitEvent(event) {
  return function(doc) {
    TravelportHotelEvents.emit(event + ':' + doc._id, doc);
    TravelportHotelEvents.emit(event, doc);
  };
}

export default TravelportHotelEvents;
